import tkiQrcode from '@/components/tki-qrcode/tki-qrcode.vue'
    import {  
        mapState
    } from 'vuex';  
    export default {  
		data() {
			return {
				memberInfo:[],
				isShop:false
			}				
		},
        computed: mapState(['hasLogin','userInfo']),  
		components: {tkiQrcode}, 
		onLoad(e) {
			 console.log('页面加载');
			 console.log(e);
			 if(e.t=='shop'){
				this.isShop=true;
			 }
			this.loadData();
		},
		 methods: {   
			async loadData() {  
				console.log('===userInfo==='+this.hasLogin);
				console.log(this.userInfo);
				console.log(this.userInfo.token);
				if (!this.hasLogin) {  
					uni.navigateTo({
						url:'login',
					})
				}else{
					this.$Request.post('member_invite').then(res => {
						if (res.code == 200) {
							console.log(res);
							if(res.datas.is_realverify!=1){
								console.log('请先实名验证！');
								uni.showToast({
									icon: 'none',
									title:'请先实名验证！',
									duration:5000,
									success: function () {
										uni.redirectTo({
											url:'set/realverify',
										});
									}
								});
							}else{
								this.memberInfo = res.datas.member_info;
							}
						}
						else{
							uni.showToast({
								icon: 'none',
								title:res.datas.error,
								duration:2000
							});
							uni.navigateTo({
								url:'login',
							});
						}
					});
				}
			},
			toPage: function(url) {
				uni.navigateTo({
					url:url,
				})
			},
			showShop:function(){
				this.isShop=true;
			},
			showUser:function(){
				this.isShop=false;
			},
			qrR(res) {
				//this.src = res
			},
			/* ,
			share(type) {
				
				uni.share({
					provider: "weixin",
					scene: "WXSceneSession",
					type: 0,
					href: "http://uniapp.dcloud.io/",
					title: "uni-app分享",
					summary: "我正在使用HBuilderX开发uni-app，赶紧跟我一起来体验！",
					imageUrl: "https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/uni@2x.png",
					success: function (res) {
						console.log("success:" + JSON.stringify(res));
					},
					fail: function (err) {
						console.log("fail:" + JSON.stringify(err));
					}
				});
					
			} */
			 
		}  
	}  
